import React from "react";
import {connect} from "react-redux";
import ProductGalleryComponent from "eshop/modules/simfree/components/gallery/ProductGalleryComponent";
import ComparatorCheckbox from "eshop/modules/simfree/components/comparator/ComparatorCheckbox";
import {setSelectedBaseDeviceCode} from "eshop/modules/simfree/actions/app";
import {getProductImageUrl, getProductName, getSelectedVariant} from "eshop/modules/simfree/selectors";
import {getSelectedProcessTypeValue} from "eshop/modules/configurator/selectors/filters";
import {changeCategory} from "../../actions/app";

class ProductDetailsDeviceComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentWillMount() {

    }

    findProductVariant() {

        for (var i = 0; i < this.props.deviceData.variantList.length; i++) {
            if (this.props.deviceData.variantList[i].productId == this.props.selectedVariant
                || (!this.props.selectedVariant && this.props.deviceData.variantList[i].productId == this.props.deviceData.selectedVariant)) {

                return this.props.deviceData.variantList[i];
            }
        }
    }

    componentDidMount() {

        this.props.setSelectedBaseDeviceCode(this.props.deviceData.productId)
        OPL.UI.loadModules(document.getElementById('sticked-container'), [
            {
                path: "common/modules/opl-sticker",
                options: {stayDown: true},
                conditions: "media:{gt-medium}"

            }]);
        this.props.changeCategory({code : this.props.deviceData.deviceCategory});
    }

    render() {

        return (

            <div ref={ref => this.ref = ref}>
                <div
                    className="l-col l-col-small-12 l-col-medium-12  u-large-hide u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <h2 data-skiplinks="Podstawowe informacje o produkcie" className="h1 u-medium-no-spacing"
                        tabIndex="-1" id="skiplinks831">{this.props.deviceData.name}</h2>
                </div>
                {this._renderRatingStars()}

                <div
                    className="l-col l-col-small-12 l-col-medium-12 l-col-6  u-text-center u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l u-position-static">
                    <ProductGalleryComponent productVariant={this.findProductVariant()}
                                             sticker={this.getStickerForSelectedProcess()}/>
                </div>
            </div>

        )
    }

    getStickerForSelectedProcess() {
        if (this.props.deviceData.sticker && this.props.deviceData.sticker.availableProcesses.includes(this.props.selectedProcess)) {
            return this.props.deviceData.sticker;
        }
        return null;
    }

    _renderRatingStars() {
        return (
            <div className="l-col l-col-small-12 l-col-8">
                <div
                    className="l-col l-col-small-12 l-col-medium-6 u-large-hide u-padding-m u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <div className="opl-rating">
                        <span className="g-icon g-icon--star g-icon--s"></span><span
                        className="g-icon g-icon--star g-icon--s"></span><span
                        className="g-icon g-icon--star g-icon--s"></span><span
                        className="g-icon g-icon--star g-icon--s"></span><span
                        className="g-icon g-icon--star g-icon--s"></span>
                        <div style={{width: this.props.deviceData.percentageRating}}
                             className="opl-rating-bar"><span
                            className="g-icon g-icon--star g-icon--s"></span><span
                            className="g-icon g-icon--star g-icon--s"></span><span
                            className="g-icon g-icon--star g-icon--s"></span><span
                            className="g-icon g-icon--star g-icon--s"></span><span
                            className="g-icon g-icon--star g-icon--s"></span></div>
                    </div>
                    <p className="small g-gray3-c u-inline-block">
                        ({this.props.deviceData.numberOfReviewsCustomers})</p>
                </div>
                <div
                    className="l-col l-col-small-12 l-col-medium-4 u-small-padding-top-s u-medium-padding-top-s u-large-hide u-medium-padding-left-l u-small-padding-left-l u-medium-padding-right-l u-small-padding-right-l">
                    <ComparatorCheckbox product={this.props.deviceData}/>
                </div>
            </div>
        )
    }

}

const mapStateToProps = state => ({
    selectedVariant: getSelectedVariant(state),
    imageUrl: getProductImageUrl(state),
    selectedProcess: getSelectedProcessTypeValue(state)
});

const mapDispatchToProps = (dispatch) => ({
    setSelectedBaseDeviceCode: (deviceCode) => dispatch(setSelectedBaseDeviceCode(deviceCode)),
    changeCategory: (category) => dispatch(changeCategory(category))

});

export default connect(mapStateToProps, mapDispatchToProps)(ProductDetailsDeviceComponent);
